import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WorldreportService } from '../_services/worldreport.service';
import { World } from '../_modules/World';


@Component({
  selector: 'app-info-card-world-wide',
  templateUrl: './info-card-world-wide.component.html',
  styleUrls: ['./info-card-world-wide.component.css']
})
export class InfoCardWorldWideComponent implements OnInit {
  dataSource: World;
  statusbar = false;
  constructor(private route: Router, private worldservice: WorldreportService) { }

  ngOnInit(): void {
        this.worldservice.getWorldWideReport().then(response => {
          this.dataSource = response as World;
          this.statusbar = true;
        });
  }
  redirectToStatistic() {
    this.route.navigate(['/Statistic']);
  }
}
