import { Component, OnInit } from '@angular/core';
import { Continent } from '../_modules/Continent';
import { ContinentreportService } from '../_services/continentreport.service';

@Component({
  selector: 'app-continent-main',
  templateUrl: './continent-main.component.html',
  styleUrls: ['./continent-main.component.css']
})
export class ContinentMainComponent implements OnInit {

  dataSource;
  statusbar = false;
 // tslint:disable-next-line: whitespace

  constructor(private continentReport: ContinentreportService) { }

  ngOnInit(): void {
    this.continentReport.getContinentReport().then(response => {
        this.dataSource = response as Continent[];
        this.statusbar = true;
    });
  }


}
