import { Component, OnInit, NgZone, AfterViewInit, ChangeDetectionStrategy, ViewChild } from '@angular/core';
import { ReportsCountryServiceService } from '../_services/reports-country-service.service';
import { country } from '../_modules/Country';
import * as AOS from 'aos';
import { MatDialog } from '@angular/material/dialog';
import { NgxSpinnerService } from 'ngx-spinner';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
@Component({
  selector: 'app-table-main',
  templateUrl: './table-main.component.html',
  styleUrls: ['./table-main.component.css'],
  changeDetection: ChangeDetectionStrategy.Default,

})
export class TableMainComponent implements OnInit {

  @ViewChild(CdkVirtualScrollViewport, { static: false }) viewport: CdkVirtualScrollViewport;

  dataSource;
  errorInternet = false;
  sortMode: any;
  termAsearching: string;
  constructor(private ReportCountriesInfo: ReportsCountryServiceService,
    public dialog: MatDialog,
    private spinner: NgxSpinnerService) { }

  ngOnInit() {
    AOS.init();
    this.spinner.show();
    this.ReportCountriesInfo.getreportcountries().then(response => {
      this.spinner.hide();
      this.dataSource = response as unknown as country[];
    }, err => {
      this.errorInternet = true;
      this.dialog.open(Alertfield);
    });
  }
  // sort
  sorting(SortMode: string) {
    this.spinner.show();
    switch (SortMode) {
      case 'Confirmed':
        this.srt('cases');
        break;
      case 'Deaths':
        this.srt('deaths');
        break;
      case 'Recovereds':
        this.srt('recovered');
        break;
    }
  }
  srt(field) {
    this.ReportCountriesInfo.getreportcountriesWithSort(field).then(response => {
      this.spinner.hide();
      this.viewport.scrollToIndex(0);
      this.dataSource = response as unknown as country[];
    }, err => {
      this.errorInternet = true;
      this.dialog.open(Alertfield);
    });
  }
scrollToTop() {
  window.scroll({
    top: 0,
    left: 0,
    behavior: 'smooth'
  });
}

}

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'Alertfield',
  templateUrl: 'Alert-field.html',
})
// tslint:disable-next-line: component-class-suffix
export class Alertfield { }
