import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-drop-down-sort',
  templateUrl: './drop-down-sort.component.html',
  styleUrls: ['./drop-down-sort.component.css']
})
export class DropDownSortComponent implements OnInit {
  @Output() sortingEvent = new EventEmitter();
  SortMode: string;
  values=["Confirmed","Deaths","Recovereds"]
  constructor() { }

  ngOnInit(): void {
  }
  callMethodeAlerty(val) {
    this.sortingEvent.emit(val);
  }
}
