import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { TableMainComponent } from './table-main/table-main.component';
import { InfoCountryComponent } from './info-country/info-country.component';


const routes: Routes = [
  {
    path : '',
    redirectTo : '/Home',
    pathMatch: 'full'
  },
  {
    path: 'Home',
    component: HomeComponent,
    pathMatch: 'full'
  },
  {
    path: 'Statistic',
    component: TableMainComponent,
    pathMatch: 'full'
  },
  {
    path: 'MoreInfo/:country',
    component: InfoCountryComponent,
    pathMatch: 'full'
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
