import { Component, OnInit, Input } from '@angular/core';
import { ChartDataSets, ChartOptions, ChartType } from 'chart.js';
import { Color, Label } from 'ng2-charts';
import { ReportsCountryServiceService } from '../_services/reports-country-service.service';
import { HistoriqueCountry } from '../_modules/HistoriqueCountry';

@Component({
  selector: 'app-my-line-chart',
  templateUrl: './my-line-chart.component.html',
  styleUrls: ['./my-line-chart.component.css']
})
export class MyLineChartComponent implements OnInit {
  @Input() countryIn;
  Data: HistoriqueCountry;
  tableKeys = new Array();
  tableValuesCases = new Array();
  tableValuesDeaths = new Array();
  tableValuesRecovered = new Array();
  from:string;
  to:string;


  public lineChartData: ChartDataSets[] = [
    { data: this.tableValuesCases, label: 'Cases' },
    { data: this.tableValuesDeaths, label: 'Deaths' },
    { data: this.tableValuesRecovered, label: 'Recovereds' },
  ];
  public lineChartLabels: Label[] = this.tableKeys;
  public lineChartOptions: ChartOptions = {
    responsive: true,

  };
  public lineChartColors: Color[] = [
    { // cases
      backgroundColor: 'rgba(255, 193, 7,0.3)',
      borderColor: 'rgba(255, 193, 7,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    },
    { // deaths
      backgroundColor:'rgba(220, 53, 69,0.5)',
      borderColor: 'rgba(220, 53, 69,1)',
      pointBackgroundColor: 'rgba(77,83,96,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(77,83,96,1)'
    },
    { // recorvered
      backgroundColor: 'rgba(40, 167, 69,0.7)',
      borderColor: 'rgba(40, 167, 69,1)',
      pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(148,159,177,0.8)'
    }
  ];
  public lineChartLegend = true;
  public lineChartType: ChartType = 'line';
  public lineChartPlugins = [];

  constructor(private ReportsCountryService: ReportsCountryServiceService) { }

  ngOnInit() {
    this.ReportsCountryService.getStatisticCountry(this.countryIn).then(res => {
      this.Data = res as HistoriqueCountry;
      this.fillCases();
      this.fillDeaths();
      this.fillRecorvered();

    })

  }
  fillCases() {
    for (let [key, value] of Object.entries(this.Data.timeline.cases)) {
      this.tableKeys.push(`${key}`);
      this.tableValuesCases.push(`${value}`);
    }
    this.to=this.tableKeys.pop();
    this.from=this.tableKeys.shift();
  }
  fillDeaths() {
    for (let [key,value] of Object.entries(this.Data.timeline.deaths)) {
      this.tableValuesDeaths.push(`${value}`);
    }
  }
  fillRecorvered() {
    for (let [key,value] of Object.entries(this.Data.timeline.recovered)) {
      this.tableValuesRecovered.push(`${value}`);
    }
  }
}
