import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ContinentreportService {

  constructor(private http: HttpClient) { }

  getContinentReport()
  {
    const url = 'https://corona.lmao.ninja/v2/continents?yesterday=false&sort=cases';
    return this.http.get(url).toPromise();
  }
}
