import { PipeTransform, Pipe } from '@angular/core';
import { country } from '../_modules/Country';


@Pipe({
  name: 'filterCountires'
})
export class filterCountires implements PipeTransform {
  termAsearching: string;
  transform(dataSource: country[], termAsearching: string) {

    if (!dataSource || !termAsearching) {
      return dataSource;
    }
    return dataSource.filter(function (el) {
      return el.country.toLowerCase().indexOf(termAsearching.toLowerCase()) !== -1;
    });
  }
}

