import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ReportsCountryServiceService } from '../_services/reports-country-service.service';
import { country } from '../_modules/Country';
import * as moment from 'moment';

@Component({
  selector: 'app-info-country',
  templateUrl: './info-country.component.html',
  styleUrls: ['./info-country.component.css']
})
export class InfoCountryComponent implements OnInit {
  DateNow: string;
  @Input() themeValue;
  countryInfo: country = null;
  constructor(private route: ActivatedRoute, private ReportsCountryService: ReportsCountryServiceService) { }

  ngOnInit() {
    this.DateNow = moment().format('MMM Do YY');
    this.ReportsCountryService.getCountryInfo(this.route.snapshot.params.country).then(res => {

       return this.countryInfo = res as country;
      });
  }

}
